<template>
  <div>
    <div class="pt-1">
      <span v-show="!loading" v-for="(page, key) in pages" :key="key" @click="loadPage(page)">
        {{ page }} , 
      </span>
      <span v-show="loading">Loading...</span>
    </div>
    <div class="container">
      <div class="row">
        <div v-for="user in users" :key="user.id" class="col-4 mt-1 bg-light">
          <div><img :src="user.profile.photo" height="100" alt="" srcset=""></div>
          <div>{{ user.profile.username }}</div>
          <div>{{ user.profile.name }}</div>
        </div>
      </div>
      <div>
      </div>
    </div>
    <div class="pt-1">
      <span v-show="!loading" v-for="(page, key) in pages" :key="key" @click="loadPage(page)">
        {{ page }} , 
      </span>
      <span v-show="loading">Loading...</span>
    </div>
  </div>
</template>

<script>
import UserService from '@/services/user.service';

export default {
  data() {
    return {
      loading: false,
      users: [],
      meta: [],
      randomSeed: Math.floor(Math.random() * 99999999) + 11111111
    }
  },
  created() {
    this.loadPage(1)
  },
  computed: {
    pages: function() {
      let pages = [];
      for (let index = 1; index <= this.meta.last_page; index++)
        pages.push(index);
      return pages;
    }
  },
  methods: {
    loadPage(page, perPage = 15) {
      this.loading = true;
      UserService.getTodayBirthdays(page, perPage, this.randomSeed)
        .then(res=> {
          this.users = res.data.data;
          this.meta = res.data.meta;
          console.log(res.data)
        })
        .then(()=> {
          this.loading = false;
        })
    ;
    }
  }
}
</script>