import Ax from './Ax';

class UserService {

  getTodayBirthdays(page = 1, perPage = 15, randomSeed = 0)
  {
    return Ax.get('/explore/birthday', {
      params: {
        page,
        per_page: perPage,
        rnd: randomSeed
      }
    });
  }

  searchUser(searchText, page, perPage) {
    return Ax.post('/users/search', {
      q: searchText,
      page,
      per_page: perPage
    })
      .then(res=> Promise.resolve(res.data))
      .catch(err=> Promise.reject(err));
  }

}

export default new UserService();